/**
 * 31. Testowanie silnej spjnoci grafu
 *
 * Reducer zawiera wszystkie funkcje obliczajce siln spjno grafu.
 * Zmienia w modelu flag `isGraphConnected`, gdzie `true` oznacza graf silnie spjny, `false` mwi o braku takiej spjnoci,
 * a `null` oznacza stan pocztkowy flagi
 */
import {createEmptyArray, Stack} from '../helpers';

export const CHECK_GRAPH_CONNECTIVITY = 'connectivity/CHECK_GRAPH_CONNECTIVITY';
export const RESET_STATUS = 'RESET_STATUS';
export const BLOCK_GRAPH_INPUTS = 'BLOCK_GRAPH_INPUTS';

/**
 * Gwna funkcja, ktr dostarcza ten reducer to `connectivity()`.
 *
 * - Gdy zostanie wywoana akcja CHECK_GRAPH_CONNECTIVITY i gdy graf jest silnie spjny,
 *   funkcja zmienia flag `isGraphConnected` na `true`. W przeciwnym razie na `false`.
 * - Gdy zostanie wywoana akcja RESET_STATUS, flaga jest ustawiona na `null` (warto domylna tej flagi)
 *
 * Gdy akcja to CHECK_GRAPH_CONNECTIVITY:
 *   Jako pierwszy parametr przyjmuje aktualny stan aplikacji (a dokadniej wycinek stanu odpowiadjcego za spjno grafu)
 *   Jako drugi parametr dostaje akcj, wraz z grafem, ktrego spjno ma sprawdza.
 * Przyjmowana akcja ma tak posta:
 * action = {
 *   type: CHECK_GRAPH_CONNECTIVITY,
 *   graph: macierz_sasiedztwa
 * }
 * gdzie `macierz_sasiedztwa` moe wyglda w taki sposb:
 * [
 *   [1, 0, 1],
 *   [0, 1, 0],
 *   [1, 1, 1],
 * ];
 *
 * Gdy akcja to RESET_STATUS:
 * Reducer moe przyj te drug akcj, ktra resetuje informacj o sprawdzanym grafie (czyli ustawia flag na `null`):
 *  action = {
 *   type: RESET_STATUS,
 * }
 */
export default function connectivity(state = [], action) {
  switch (action.type) {
    case CHECK_GRAPH_CONNECTIVITY:
      const newState = {...state};
      newState.isGraphConnected = checkStrongConnectivityOfGraph(action.graph);
      return newState;

    case RESET_STATUS:
      const resettedState = {...state};
      resettedState.isGraphConnected = null;
      return resettedState;

    case BLOCK_GRAPH_INPUTS:
      const blockedState = {...state};
      blockedState.blockGraphInputs = action.block;
      return blockedState;

    default:
      return state;
  }
}

/**
 * Sprawdza siln spjno grafu.
 * Funkcja przyjmuje graf w formie macierzy ssiedztwa.
 *
 * @param graph
 * @returns {boolean} - true, jeli graf jest silnie spjny
 */
function checkStrongConnectivityOfGraph(graph) {
  for (const node in graph) {
    const result = checkConnectivityOfGraph(graph, node);
    if (result === false) {
      return false;
    }
  }
  return true;
}

/**
 * Sprawdza spjno grafu
 * Funkcja przyjmuje graf w formie macierzy ssiedztwa.
 * Zwraca `true`, jeli graf jest spjny, bd false w przeciwnym wypadku
 *
 * @param graph - graf w formie macierzy ssiedztwa
 * @param startedNode - wierchoek, z ktrego startujemy, domylnie pierwszy (czyli 0)
 * @return boolean
 */
function checkConnectivityOfGraph(graph, startedNode = 0) {
  const visited = createEmptyArray(graph.length, false);
  const stack = new Stack();
  let visitedCounter = 0;

  stack.push(startedNode);
  visited[startedNode] = true;

  while (!stack.empty()) {
    const v = stack.pop();
    visitedCounter++;

    for (const neighbour in graph[v]) {
      if (graph[v][neighbour] === 0) {
        continue;
      }
      if (visited[neighbour]) {
        continue;
      }
      stack.push(neighbour);
      visited[neighbour] = true;
    }
  }
  return visitedCounter === graph.length;
}



