import { changeGraphSize, copyMatrix, createEmptyMatrix, listaNaMacierz } from '../helpers';
import { CHANGE_GRAPH_SIZE } from './graphSizeRecuder';

export const MACIERZ_SASIEDZTWA_CHANGED = 'matrix/MACIERZ_SASIEDZTWA_CHANGED';
export const LISTY_SASIEDZTWA_CHANGED = 'matrix/LISTY_SASIEDZTWA_CHANGED';
export const CREATE_EMPTY_MATRIX = 'matrix/CREATE_EMPTY_MATRIX';

function matrixReduxer(state = [], action) {
  switch (action.type) {
    case MACIERZ_SASIEDZTWA_CHANGED: // graf w formie macierzy ssiedztwa si zmieni - wystarczy go zapisa do store
      const copiedMatrix = copyMatrix(state);
      copiedMatrix[action.row][action.col] = action.value;
      return copiedMatrix;
    case LISTY_SASIEDZTWA_CHANGED: // graf w formie listy ssiedztw si zmieni - trzeba go zamieni na macierz ssiedztwa i zapisa w store
      return listaNaMacierz(action.lists);
    case CREATE_EMPTY_MATRIX:
      return createEmptyMatrix(action.size, 0);
    case CHANGE_GRAPH_SIZE:
      return changeGraphSize(state, action.size);
    default:
      return state;
  }
}

export default matrixReduxer;


