/**
 * Tutaj znajduj si wszystkie funkcje "pomocniki", takie jak implementacja stosu, atwiejsze operowanie na tablicach itp
 */


/**
 * Funkcja pomocnicza, tworzy pust tablic tablic (macierz)
 * @param nodes - ilo wierchokw w grafie
 * @param defaultValue - warto domylna, ktr ma by wypeniona macierz
 * @return {Array}
 */
export function createEmptyMatrix(nodes, defaultValue = null) {
  let matrix = [];
  for (let i = 0; i < nodes; i++) {
    matrix[i] = [];
    for (let j = 0; j < nodes; j++) {
      matrix[i][j] = defaultValue;
    }
  }
  return matrix;
}

/**
 * Wykonuje 'deep copy' tablicy dwu wymiarowej (macierzy)
 * @param matrix - array[][]
 * @returns {array[][]}
 */
export function copyMatrix(matrix) {
  return matrix.map(current => current.map(value => value));
}

export function immutablePush(arr, newEntry) {
  return [...arr, newEntry]
}

/**
 * Funkcja pomocnicza, tworzy pust tablic i wypenia j wartociami
 * @param length - dugo tablicy
 * @param defaultValue - warto domylna, ktr ma by wypeniona tablica. Tylko typ prosty. NIE MOE TO BYC OBIEKT ANI TABLICA! (powstaje bd referencji)
 * @return {Array}
 */
export function createEmptyArray(length, defaultValue = null) {
  let matrix = [];
  for (let i = 0; i < length; i++) {
    matrix[i] = defaultValue;
  }
  return matrix;
}

/**
 * Zamienia macierz ssiedztwa na listy ssiedztwa
 * @param matrix - macierz ssiedztwa
 * @return {Array[][]} - graf w formie list ssiedztwa
 */
export function macierzNaListe(matrix) {
  const result = createEmptyArray(matrix.length);
  matrix.map((row, i) => {
    result[i] = [];
    row.map((value, j) => {
      if (value !== 0) {
        result[i].push(j);
      }
    })
  });
  return result;
}

/**
 * Zamienia listy ssiedztwa na macierze ssiedztwa
 * @param lists - listy ssiedztwa
 * @return {Array[][]} - graf w formie macieerzy ssiedztwa
 */
export function listaNaMacierz(lists) {
  let result = createEmptyMatrix(lists.length, 0);
  lists.map((row, index) =>
    row.map(value => {
      if (Number.isInteger(value)) {
        result[index][value] = 1;
      }
    }));
  return result;
}

/**
 * Implementacja stosu
 */
export class Stack {
  constructor() {
    this.items = [];
    this.count = 0;
  }

  getLength() {
    return this.count;
  }

  push(item) {
    this.items.push(item);
    this.count = this.count + 1;
  }

  pop() {
    if (this.count > 0) {
      this.count = this.count - 1;
    }

    return this.items.pop();
  }

  peek() {
    return this.items.slice(-1)[0];
  }

  empty() {
    return !this.items.length;
  }
}

/**
 * Change matrix Size
 *
 * @param {Array[][]} matrix
 * @param newSize - new size of matrix
 * @returns {Array}
 */
export function changeGraphSize(matrix, newSize) {
  const result = createEmptyMatrix(newSize, 0);
  const size = matrix.length > newSize ? newSize : matrix.length;
  for (let i = 0; i < size; i++) {
    for (let j = 0; j < size; j++) {
      result[i][j] = matrix[i][j];
    }
  }
  return result;
}


